.header {
   position: sticky;
   top: 0;
   width: 100%;
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 20px 30px;
   background-color: white;
   /* box-shadow: -1px 5px 10px #cbcbcb; */
   z-index: 1;
 }
 
 .left_ {
   flex-basis: 70%;
   display: flex;
   align-items: center;
 }
 
 .right_ {
   display: flex;
   flex-basis: 30%;
   align-items: center;
   flex-direction: row;
   justify-content: flex-end;
 }
 
 .input_wrapper {
   width: 100%;
   position: relative;
 }
 
 .input_wrapper input {
   width: 60%;
   padding: 10px;
   outline: none;
   border: none;
   background-color: rgb(241, 241, 241);
   border-radius: 20px;
   padding-left: 40px;
 }
 
 @media (max-width: 700px) {
   .header {
     padding: 10px;
   }
 
   .input_wrapper input {
     width: 90%;
   }
 }
 
 .search_icon {
   position: absolute;
   top: 10px;
   left: 10px;
 }
 
 .small_icons {
   position: relative;
   width: 35px;
   height: 35px;
   background-color: rgb(0, 0, 0);
   display: flex;
   justify-content: center;
   align-items: center;
   border-radius: 50px;
   margin-right: 20px;
 }
 
 @media (max-width: 600px) {
   .small_icons {
     margin-right: 10px;
   }
 
   .search_icon {
     position: absolute;
     top: 14px;
     left: 14px;
   }
 }
 
 .floating_text {
   position: absolute;
   top: -5px;
   right: -5px;
   padding: 4px;
   /* background-color: rgb(0, 0, 0); */
   border-radius: 50%;
   background-color: white;
   border: 1px solid black;
 }
 
 .floating_text p {
   font-size: 12px;
   color: black !important;
 }
 
 .heart_wrapper {
   background-color: rgba(226, 190, 122, 0.403);
 }
 
 .user_profile {
   display: flex;
   align-items: center;
   justify-content: center;
 }
 
 .user_profile {
   width: 35px;
   height: 35px;
   border-radius: 50%;
   object-fit: cover;
   margin-left: 10px;
   background-color: black;
   overflow: hidden;
 }
 .user_profile img {
   width: 100%;
   height: 100%;
   border-radius: 50%;
   object-fit: cover;
   margin-left: 10px;
 }
 
 .user_profile p {
   font-size: 13px;
 }
 
 .logo img {
   width: 40px;
   height: 40px;
   border-radius: 50%;
   margin-right: 10px;
   background-color: black;
 }
 
 .right_ .small_icons p {
   color: white;
 }
 
 @media (max-width: 600px) {
   .logo {
     /* display: none; */
   }
 }
 
 
 .login_btn {
   padding: 7px 20px;
   color: black;
   font-weight: bold;
   border: 1px solid black;
   margin-left: 10px;
 }
 
 .user_profile span {
   color: white;
 }


 .mobile_sidebar {
  display: none;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.78);
}

.wrapper {
  background-color: rgb(0, 0, 0);
  width: 70%;
  height: 100vh;
  padding-top: 10px;
}

.close_wrapper {
  float: right;
  padding: 0px 10px;
}
.menu {
  display: none;
}
@media (max-width: 600px) {
  .mobile_sidebar {
    display: block;
  }
  
  .menu {
    display: block;
  }
}

.right_  svg{
  margin-left: 20px;
}

